div.toppage{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 50%;
    transform: translateX(-50%);
    font-family: "Century Gothic", sans-serif;
}

div.toppage h1 {
    color: white;
}

div.toppage h2{
    color:white;
    position: relative;
    text-align: center;
    font-family: "Century Gothic", sans-serif;
}

div.toppage h2.SmartPhoneMessage {
    color: transparent;
}

@media only screen and (max-width: 767px) {
    div.toppage h2.SmartPhoneMessage {
        color: white;
    } 
}